.Heart {
  width: 100%;
  min-height: 100vh;
  padding: 1% 6.5%;
  background-color: #E5E5E5;
}

.Heart .Heart-header h1 {
  font-family: Canaro;
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  letter-spacing: 1px;
  line-height: 48px;
  color: #000000;
}

.Heart .Card .card {
  position: relative;
}

.Heart .Card .card .heart {
  position: absolute;
  top: 4%;
  left: 5%;
  font-size: 30px;
  color: red;
}

@media screen and (max-width: 426px) {
  .Heart .Heart-header h1 {
    font-size: 25px;
  }
  .Heart .Card .card {
    position: relative;
  }
  .Heart .Card .card .heart {
    position: absolute;
    top: 4%;
    left: 5%;
    font-size: 20px;
    color: red;
  }
}
